"use client"

import { Button } from "@/components/ui/button"
import { Card } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { Textarea } from "@/components/ui/textarea"
import Link from "next/link"
import { useState } from "react"

export default function VirtualBranchPage() {
  const [isSidebarOpen, setIsSidebarOpen] = useState(false)
  const [isLoading, setIsLoading] = useState(false)

  const accounts = [
    { id: "acc1", number: "**** 1234", type: "Current Account", balance: "50,000.00" },
    { id: "acc2", number: "**** 5678", type: "Savings Account", balance: "25,000.00" },
  ]

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setIsLoading(true)
    // Simulate API call
    await new Promise((resolve) => setTimeout(resolve, 1000))
    setIsLoading(false)
  }

  return (
    <div className="min-h-screen flex">
      {/* Sidebar */}
      <aside
        className={`fixed md:static inset-y-0 left-0 z-50 w-64 bg-white border-r transform transition-transform duration-200 ease-in-out ${
          isSidebarOpen ? "translate-x-0" : "-translate-x-full md:translate-x-0"
        }`}
      >
        <div className="p-4 border-b">
          <Link href="/" className="flex items-center space-x-2">
            <span className="text-2xl font-bold text-[#c0985b]">ACL Digital</span>
          </Link>
        </div>

        <nav className="p-4 space-y-2">
          {[
            { href: "/dashboard", label: "Dashboard" },
            { href: "/transfers", label: "Transfers" },
            { href: "/bill-payments", label: "Bill Payments" },
            { href: "/bulk-payments", label: "Bulk Payments" },
            { href: "/visa-management", label: "Visa Cards" },
            { href: "/virtual-branch", label: "Virtual Branch" },
          ].map((item) => (
            <Link
              key={item.href}
              href={item.href}
              className={`flex items-center space-x-2 px-4 py-2 rounded-lg text-gray-600 hover:bg-gray-100 hover:text-[#c0985b] ${
                item.href === "/virtual-branch"
                  ? "bg-[#c0985b]/10 text-[#c0985b]"
                  : ""
              }`}
            >
              <span>{item.label}</span>
            </Link>
          ))}
        </nav>
      </aside>

      {/* Main Content */}
      <div className="flex-1 min-h-screen bg-gray-50">
        {/* Header */}
        <header className="bg-white border-b sticky top-0 z-40">
          <div className="flex items-center justify-between p-4">
            <button
              className="md:hidden p-2"
              onClick={() => setIsSidebarOpen(!isSidebarOpen)}
            >
              <svg
                className="w-6 h-6"
                fill="none"
                stroke="currentColor"
                viewBox="0 0 24 24"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d="M4 6h16M4 12h16M4 18h16"
                />
              </svg>
            </button>
            <div className="flex items-center space-x-4">
              <span className="text-sm text-gray-600">Welcome, John Doe</span>
              <Button variant="outline" asChild>
                <Link href="/login">Logout</Link>
              </Button>
            </div>
          </div>
        </header>

        {/* Main Content */}
        <main className="p-6">
          <div className="max-w-2xl mx-auto">
            <Card className="p-6">
              <div className="space-y-6">
                <div>
                  <h1 className="text-2xl font-bold">Virtual Branch</h1>
                  <p className="text-gray-600 mt-2">
                    Request deposits and withdrawals from your accounts
                  </p>
                </div>

                <Tabs defaultValue="deposit" className="space-y-6">
                  <TabsList className="grid w-full grid-cols-2">
                    <TabsTrigger value="deposit">Deposit</TabsTrigger>
                    <TabsTrigger value="withdrawal">Withdrawal</TabsTrigger>
                  </TabsList>

                  {/* Deposit Form */}
                  <TabsContent value="deposit">
                    <form onSubmit={handleSubmit} className="space-y-6">
                      <div className="space-y-4">
                        <div className="space-y-2">
                          <Label>Account</Label>
                          <Select>
                            <SelectTrigger>
                              <SelectValue placeholder="Select account" />
                            </SelectTrigger>
                            <SelectContent>
                              {accounts.map((account) => (
                                <SelectItem key={account.id} value={account.id}>
                                  {account.type} - {account.number} (${account.balance})
                                </SelectItem>
                              ))}
                            </SelectContent>
                          </Select>
                        </div>

                        <div className="space-y-2">
                          <Label>Amount</Label>
                          <div className="relative">
                            <span className="absolute left-3 top-1/2 -translate-y-1/2 text-gray-500">
                              $
                            </span>
                            <Input
                              type="number"
                              placeholder="0.00"
                              className="pl-8"
                              required
                            />
                          </div>
                        </div>

                        <div className="space-y-2">
                          <Label>Deposit Method</Label>
                          <Select>
                            <SelectTrigger>
                              <SelectValue placeholder="Select method" />
                            </SelectTrigger>
                            <SelectContent>
                              <SelectItem value="cash">Cash Deposit</SelectItem>
                              <SelectItem value="check">Check Deposit</SelectItem>
                              <SelectItem value="transfer">Bank Transfer</SelectItem>
                            </SelectContent>
                          </Select>
                        </div>

                        <div className="space-y-2">
                          <Label>Notes</Label>
                          <Textarea
                            placeholder="Add any additional information"
                            className="min-h-[100px]"
                          />
                        </div>
                      </div>

                      <Button
                        type="submit"
                        className="w-full bg-[#c0985b] hover:bg-[#b08a4d]"
                        disabled={isLoading}
                      >
                        {isLoading ? "Processing..." : "Request Deposit"}
                      </Button>
                    </form>
                  </TabsContent>

                  {/* Withdrawal Form */}
                  <TabsContent value="withdrawal">
                    <form onSubmit={handleSubmit} className="space-y-6">
                      <div className="space-y-4">
                        <div className="space-y-2">
                          <Label>Account</Label>
                          <Select>
                            <SelectTrigger>
                              <SelectValue placeholder="Select account" />
                            </SelectTrigger>
                            <SelectContent>
                              {accounts.map((account) => (
                                <SelectItem key={account.id} value={account.id}>
                                  {account.type} - {account.number} (${account.balance})
                                </SelectItem>
                              ))}
                            </SelectContent>
                          </Select>
                        </div>

                        <div className="space-y-2">
                          <Label>Amount</Label>
                          <div className="relative">
                            <span className="absolute left-3 top-1/2 -translate-y-1/2 text-gray-500">
                              $
                            </span>
                            <Input
                              type="number"
                              placeholder="0.00"
                              className="pl-8"
                              required
                            />
                          </div>
                        </div>

                        <div className="space-y-2">
                          <Label>Withdrawal Method</Label>
                          <Select>
                            <SelectTrigger>
                              <SelectValue placeholder="Select method" />
                            </SelectTrigger>
                            <SelectContent>
                              <SelectItem value="cash">Cash Withdrawal</SelectItem>
                              <SelectItem value="check">Check Withdrawal</SelectItem>
                              <SelectItem value="transfer">Bank Transfer</SelectItem>
                            </SelectContent>
                          </Select>
                        </div>

                        <div className="space-y-2">
                          <Label>Purpose</Label>
                          <Textarea
                            placeholder="Specify the purpose of withdrawal"
                            className="min-h-[100px]"
                          />
                        </div>
                      </div>

                      <Button
                        type="submit"
                        className="w-full bg-[#c0985b] hover:bg-[#b08a4d]"
                        disabled={isLoading}
                      >
                        {isLoading ? "Processing..." : "Request Withdrawal"}
                      </Button>
                    </form>
                  </TabsContent>
                </Tabs>

                {/* Recent Requests */}
                <div className="mt-8 pt-6 border-t">
                  <h2 className="text-lg font-semibold mb-4">Recent Requests</h2>
                  <div className="space-y-4">
                    {[
                      {
                        type: "Deposit",
                        amount: "$5,000.00",
                        date: "2024-01-20",
                        status: "Pending",
                        method: "Bank Transfer",
                      },
                      {
                        type: "Withdrawal",
                        amount: "$1,000.00",
                        date: "2024-01-18",
                        status: "Completed",
                        method: "Cash",
                      },
                    ].map((request, index) => (
                      <div
                        key={index}
                        className="flex items-center justify-between p-4 bg-gray-50 rounded-lg"
                      >
                        <div>
                          <div className="flex items-center space-x-2">
                            <span
                              className={`w-2 h-2 rounded-full ${
                                request.type === "Deposit"
                                  ? "bg-green-500"
                                  : "bg-red-500"
                              }`}
                            />
                            <p className="font-medium">
                              {request.type} - {request.method}
                            </p>
                          </div>
                          <p className="text-sm text-gray-600">{request.date}</p>
                        </div>
                        <div className="text-right">
                          <p className="font-medium">{request.amount}</p>
                          <p
                            className={`text-sm ${
                              request.status === "Completed"
                                ? "text-green-600"
                                : "text-yellow-600"
                            }`}
                          >
                            {request.status}
                          </p>
                        </div>
                      </div>
                    ))}
                  </div>
                </div>
              </div>
            </Card>
          </div>
        </main>
      </div>
    </div>
  )
}
